.replyContainer {
  border: 1px solid #E6ECF0;
  width: 45vw;
  height: 100vh;
  overflow-y: scroll;

  .topSection {
    border-bottom: 10px solid #E6ECF0;

    .title {
      display: flex;
      align-items: center;
      border-bottom: 1px solid #E6ECF0;
      height: 50px;
      margin: 0;
      font-weight: 700;
      color: #171725;
      font-size: 24px;

      .iconBox {
        display: flex;
        justify-content: center;
        align-items: center;
        width: 24px;
        height: 24px;
        margin: 0 10px;
        cursor: pointer;
      }
      
      p {
        color: #171725;
        font-size: 24px;
        font-weight: 700;
      }
    }

    .tweet {

      .tweetTop {
        padding: 15px 15px 0 15px;
        display: flex;

        .avatarBox {
          width: 50px;
          height: 50px;
          cursor: pointer;
        }

        .info {
          margin-left: 15px;

          .name {
            color: #171725;
            font-size: 16px;
            font-weight: 700;
          }

          .account {
            color: #6C757D;
            font-size: 14px;
            font-weight: 400;
          }
        }
      }

      .tweetMiddle {
        border-bottom: 1px solid #E6ECF0;
        padding: 15px;

        .content {
          color: #171725;
          font-size: 24px;
          font-weight: 400;
        }

        .time {
          color: #6C757D;
          font-size: 14px;
          font-weight: 500;
          margin: 5px 0;
        }
      }

      .tweetBottom {

        .dataCount {
          width: 100%;
          display: flex;
          justify-content: flex-start;
          border-bottom: 1px solid #E6ECF0;
          padding: 15px;

          .count {
            font-size: 19;
            color: #6C757D;
            font-weight: 500;
            margin-right: 20px;

            b {
              color: #000000;
              font-weight: 700;
            }
          }
        }

        .iconSection {
          display: flex;
          justify-content: space-between;
          width: 20%;
          padding: 15px;

          .iconBox {
            width: 30px;
            height: 30px;
            cursor: pointer;
          }
        }
      }
    }
  }

  // scrollbar style 

  &::-webkit-scrollbar {
    width: 8px;
    background-color: #FAFAFA;
  }

  &::-webkit-scrollbar-thumb {
    background-color: #C1C1C1;
    border-radius: 4px;
    border: 2px solid #FAFAFA;
  }
}

// none
.none {
  width: 100%;
  text-align: center;
  font-size: 20px;
}