.selectionBar {
  display: flex;
  border-bottom: 1px solid #E6ECF0;
  
  .option {
    display: flex;
    justify-content: center;
    align-items: center;
    width: 130px;
    padding: 15px;
    color: #657786;
    font-size: 15px;
    font-weight: 700;
    cursor: pointer;
    z-index: 9;
  }

  .optionActive {
    display: flex;
    justify-content: center;
    align-items: center;
    width: 130px;
    padding: 15px;
    border-bottom: 3px solid #FF6600;
    color: #FF6600;
    font-size: 15px;
    font-weight: 700;
  }
}

// none
.none {
  width: 100%;
  text-align: center;
  font-size: 20px;
}