.adminSideBarWrapper {
  width: 15vw;
  display: inline-block;

  .adminSiderBarContainer {
      width: 80%;
      height: 100vh;
      margin: 0 auto;
      display: flex;
      flex-direction: column;
      justify-content: space-between;
      
      .topSection {
        .logoIconBox {
          padding-top: 8px;
          padding-bottom: 8px;
          padding-left: 6px;
          margin-bottom: 10px;
        }
        .lists {
          .list {
              width: 85%;
              display: flex;
              align-items: center;
              padding: 10px 0px;
              margin-bottom: 16px;
              cursor: pointer;

              .iconsBox {
                text-align: center;
                width: 24px;
                height: 24px;
                margin-left: 5px;
                margin-right: 12px;

                .icon {
                  width: 100%;
                  height: 100%;
                }
              }
              .pageTitle, 
              .activePage {
                font-weight: 700;
                font-size: 18px;
              }
          }
        }
      }

      .bottomSection {
        display: flex;
        align-items: center;
        justify-content: flex-start;
        padding-bottom: 16px;
        cursor: pointer;

        .iconBox {
          text-align: center;
          width: 24px;
          height: 24;
          margin-left: 5px;
          margin-right: 12px;

          .icon {
            width: 100%;
            height: 100%;
            vertical-align: middle;
          }
        }
        .titlec{
          font-weight: 700;
          font-size: 18px;
        }
      }
    }
}



.activeIcon {
  fill: #FF6600;
}

.activePage {
  color: #FF6600;
}