.postModalBox {
  position: fixed;
  display: flex;
  flex-direction: column;
  width: 45vw;
  height: 300px;
  background-color: #fff;
  border-radius: 14px;
  z-index: 10;

  .topBar {
    display: flex;
    justify-content: flex-start;
    border-bottom: 1px solid #E6ECF0;

    .iconBox {
      display: flex;
      justify-content: center;
      align-items: center;
      margin: 15px;
      cursor: pointer;
    }
  }

  .modalAvatarBox {
    padding: 20px;
    width: 50px;
    height: 50px;
    cursor: pointer;
  }

  .addTweetContent {
    position: absolute;
    width: 85%;
    height: 40%;
    top: 80px;
    right: 5px;
    font-size: 18px;
    outline: none;
    border: none;
    resize: none;

    // modal scrollbar style
    &::-webkit-scrollbar {
      width: 8px;
      background-color: #FAFAFA;
    }

    &::-webkit-scrollbar-thumb {
      background-color: #C1C1C1;
      border-radius: 4px;
      border: 2px solid #FAFAFA;
    }
  }

  .wordCount {
    position: absolute;
    bottom: 60px;
    right: 20px;
    color: #696974;
    font-size: 12px;
    font-weight: 500;    
  }

  .submitButton {
    position: absolute;
    right: 10px;
    bottom: 10px;
    width: 64px;
    height: 40px;
    border: none;
    border-radius: 50px;
    background-color: #FF6600;
    color: white;
    font-size: 16px;
    cursor: pointer;
  }
}



// validationAlert style

.alert {
  position: absolute;
  bottom: 60px;
  left: 15%;
  width: 100%;
  font-size: 12px;
  color: #FC5A5A;
  z-index: 10;
}


// button styles

.submitValid {
  position: absolute;
  bottom: 15px;
  right: 15px;
  width: 64px;
  height: 40px;
  border: none;
  border-radius: 50px;
  background-color: #FF6600;
  color: white;
  font-size: 16px;
  cursor: pointer;
}

.submitInvalid {
  position: absolute;
  bottom: 15px;
  right: 15px;
  width: 64px;
  height: 40px;
  border: none;
  border-radius: 50px;
  background-color: #828282;
  color: white;
  font-size: 16px;
}